import React, { useState, useEffect, useRef } from 'react';
import { ArrowLeft, Activity, Server, AlertCircle, CheckCircle } from 'lucide-react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { apiService } from '../services/apiService';

const TopologyPage = ({ user, onLogout }) => {
  const [nodes, setNodes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedNode, setSelectedNode] = useState(null);
  const [viewMode, setViewMode] = useState('nodes'); // nodes, pods, services
  const mountRef = useRef(null);
  const sceneRef = useRef(null);
  const rendererRef = useRef(null);
  const frameRef = useRef(null);

  // Fetch nodes data
  useEffect(() => {
    fetchNodes();
  }, []);

  const fetchNodes = async () => {
    try {
      setLoading(true);
      const response = await apiService.getTopologyNodes();
      
      if (response.success) {
        setNodes(response.nodes || []);
      } else {
        setError(response.error);
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Initialize 3D scene
  useEffect(() => {
    if (!mountRef.current || nodes.length === 0) return;

    const mountElement = mountRef.current;
    
    // Scene setup
    const scene = new THREE.Scene();
    scene.background = new THREE.Color(0x0a0a0f); // Deep space background
    sceneRef.current = scene;

    // Camera setup
    const camera = new THREE.PerspectiveCamera(
      75,
      mountElement.clientWidth / mountElement.clientHeight,
      0.1,
      1000
    );
    camera.position.set(0, 10, 20);

    // Renderer setup
    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(mountElement.clientWidth, mountElement.clientHeight);
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    mountElement.appendChild(renderer.domElement);
    rendererRef.current = renderer;

    // Controls
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true;
    controls.dampingFactor = 0.05;
    controls.maxDistance = 50;
    controls.minDistance = 5;

    // Lighting
    const ambientLight = new THREE.AmbientLight(0x404040, 0.5);
    scene.add(ambientLight);

    const pointLight = new THREE.PointLight(0x4a9eff, 1, 100);
    pointLight.position.set(10, 10, 10);
    pointLight.castShadow = true;
    scene.add(pointLight);

    const pointLight2 = new THREE.PointLight(0x00ffff, 0.5, 100);
    pointLight2.position.set(-10, -10, -10);
    scene.add(pointLight2);

    // Create nodes
    const createNodesFunc = (scene, nodesData) => {
      const radius = 8;
      const angleStep = (Math.PI * 2) / nodesData.length;

      nodesData.forEach((node, index) => {
        const angle = angleStep * index;
        const x = Math.cos(angle) * radius;
        const z = Math.sin(angle) * radius;
        const y = (Math.random() - 0.5) * 2;

        // Node geometry based on role
        let geometry, color;
        
        if (node.role === 'master') {
          geometry = new THREE.OctahedronGeometry(1.5, 0); // Master nodes are octahedrons
          color = node.health_status === 'Ready' ? 0xffd700 : 0xff4444; // Gold when ready, red when not
        } else {
          geometry = new THREE.SphereGeometry(1, 32, 32); // Worker nodes are spheres
          color = node.health_status === 'Ready' ? 0x4a9eff : 0xff4444; // Blue when ready, red when not
        }

        const material = new THREE.MeshPhongMaterial({
          color: color,
          emissive: color,
          emissiveIntensity: 0.2,
          shininess: 100,
        });

        const mesh = new THREE.Mesh(geometry, material);
        mesh.position.set(x, y, z);
        mesh.castShadow = true;
        mesh.receiveShadow = true;
        mesh.userData = {
          isNode: true,
          nodeData: node,
          offset: Math.random() * Math.PI * 2,
        };

        scene.add(mesh);

        // Add glow effect
        const glowGeometry = new THREE.SphereGeometry(geometry.parameters.radius * 1.2, 16, 16);
        const glowMaterial = new THREE.MeshBasicMaterial({
          color: color,
          transparent: true,
          opacity: 0.3,
        });
        const glow = new THREE.Mesh(glowGeometry, glowMaterial);
        mesh.add(glow);

        // Add connections between nodes
        if (index > 0) {
          const prevNode = scene.children.find(child => child.userData.isNode && child.userData.nodeData.name === nodesData[index - 1].name);
          if (prevNode) {
            createConnection(scene, prevNode.position, mesh.position);
          }
        }
      });
    };

    const createConnection = (scene, start, end) => {
      const points = [];
      points.push(start);
      points.push(end);

      const geometry = new THREE.BufferGeometry().setFromPoints(points);
      const material = new THREE.LineBasicMaterial({
        color: 0x00ffff,
        transparent: true,
        opacity: 0.6,
      });

      const line = new THREE.Line(geometry, material);
      scene.add(line);
    };

    const createParticleField = (scene) => {
      const particlesGeometry = new THREE.BufferGeometry();
      const particlesCount = 1000;
      const posArray = new Float32Array(particlesCount * 3);

      for (let i = 0; i < particlesCount * 3; i++) {
        posArray[i] = (Math.random() - 0.5) * 50;
      }

      particlesGeometry.setAttribute('position', new THREE.BufferAttribute(posArray, 3));

      const particlesMaterial = new THREE.PointsMaterial({
        size: 0.05,
        color: 0xffffff,
        transparent: true,
        opacity: 0.8,
      });

      const particlesMesh = new THREE.Points(particlesGeometry, particlesMaterial);
      scene.add(particlesMesh);
    };

    createNodesFunc(scene, nodes);
    createParticleField(scene);

    // Animation loop
    const animate = () => {
      frameRef.current = requestAnimationFrame(animate);
      
      // Rotate nodes gently
      scene.children.forEach((child) => {
        if (child.userData.isNode) {
          child.rotation.y += 0.005;
          child.position.y += Math.sin(Date.now() * 0.001 + child.userData.offset) * 0.01;
        }
      });

      controls.update();
      renderer.render(scene, camera);
    };

    animate();

    // Handle resize
    const handleResize = () => {
      if (!mountElement) return;
      camera.aspect = mountElement.clientWidth / mountElement.clientHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(mountElement.clientWidth, mountElement.clientHeight);
    };

    window.addEventListener('resize', handleResize);

    // Cleanup
    return () => {
      window.removeEventListener('resize', handleResize);
      if (frameRef.current) {
        cancelAnimationFrame(frameRef.current);
      }
      if (mountElement && renderer.domElement) {
        mountElement.removeChild(renderer.domElement);
      }
      renderer.dispose();
    };
  }, [nodes]);

  const getStatusIcon = (status) => {
    switch (status) {
      case 'Ready':
        return <CheckCircle className="w-4 h-4 text-green-400" />;
      case 'NotReady':
        return <AlertCircle className="w-4 h-4 text-red-400" />;
      default:
        return <Activity className="w-4 h-4 text-yellow-400" />;
    }
  };

  const handleBackToDashboard = () => {
    window.history.back();
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto mb-4"></div>
          <p className="text-gray-400">Loading topology data...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-center">
          <AlertCircle className="w-16 h-16 text-red-500 mx-auto mb-4" />
          <p className="text-red-400 mb-4">{error}</p>
          <button
            onClick={fetchNodes}
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg"
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white">
      {/* Header */}
      <div className="bg-gray-800 border-b border-gray-700 px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={handleBackToDashboard}
              className="flex items-center space-x-2 text-gray-300 hover:text-white transition-colors"
            >
              <ArrowLeft className="w-5 h-5" />
              <span>Back to Dashboard</span>
            </button>
            <div className="h-6 w-px bg-gray-600"></div>
            <h1 className="text-2xl font-bold flex items-center space-x-2">
              <Server className="w-6 h-6 text-blue-400" />
              <span>Cluster Topology</span>
            </h1>
          </div>
          
          {/* View Mode Selector */}
          <div className="flex items-center space-x-2">
            <span className="text-gray-400">View:</span>
            <div className="flex bg-gray-700 rounded-lg p-1">
              <button
                onClick={() => setViewMode('nodes')}
                className={`px-3 py-1 rounded ${viewMode === 'nodes' ? 'bg-blue-600 text-white' : 'text-gray-300 hover:text-white'}`}
              >
                Nodes
              </button>
              <button
                onClick={() => setViewMode('pods')}
                className={`px-3 py-1 rounded ${viewMode === 'pods' ? 'bg-blue-600 text-white' : 'text-gray-300 hover:text-white'}`}
                disabled
              >
                Pods (Coming Soon)
              </button>
              <button
                onClick={() => setViewMode('services')}
                className={`px-3 py-1 rounded ${viewMode === 'services' ? 'bg-blue-600 text-white' : 'text-gray-300 hover:text-white'}`}
                disabled
              >
                Services (Coming Soon)
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="flex h-screen pt-16">
        {/* 3D Visualization */}
        <div className="flex-1 relative">
          <div ref={mountRef} className="w-full h-full" />
          
          {/* Legend */}
          <div className="absolute top-4 left-4 bg-gray-800 bg-opacity-90 rounded-lg p-4">
            <h3 className="text-sm font-semibold mb-2">Legend</h3>
            <div className="space-y-2 text-xs">
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-yellow-400 rounded-full"></div>
                <span>Master Node</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-blue-400 rounded-full"></div>
                <span>Worker Node</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-green-400 rounded-full"></div>
                <span>Ready</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-red-400 rounded-full"></div>
                <span>Not Ready</span>
              </div>
            </div>
          </div>

          {/* Controls Hint */}
          <div className="absolute bottom-4 left-4 bg-gray-800 bg-opacity-90 rounded-lg p-4">
            <h3 className="text-sm font-semibold mb-2">Controls</h3>
            <div className="space-y-1 text-xs text-gray-300">
              <p> Left Click + Drag: Rotate</p>
              <p> Right Click + Drag: Pan</p>
              <p> Scroll: Zoom</p>
            </div>
          </div>
        </div>

        {/* Side Panel */}
        <div className="w-80 bg-gray-800 border-l border-gray-700 overflow-y-auto">
          <div className="p-4">
            <h2 className="text-lg font-semibold mb-4">Node Information</h2>
            
            {nodes.length === 0 ? (
              <p className="text-gray-400">No nodes found</p>
            ) : (
              <div className="space-y-3">
                {nodes.map((node) => (
                  <div
                    key={node.name}
                    className="bg-gray-700 rounded-lg p-3 hover:bg-gray-600 transition-colors cursor-pointer"
                    onClick={() => setSelectedNode(selectedNode?.name === node.name ? null : node)}
                  >
                    <div className="flex items-center justify-between mb-2">
                      <h3 className="font-medium text-sm">{node.name}</h3>
                      <div className="flex items-center space-x-2">
                        <span className={`text-xs px-2 py-1 rounded ${
                          node.role === 'master' ? 'bg-yellow-600' : 'bg-blue-600'
                        }`}>
                          {node.role}
                        </span>
                        {getStatusIcon(node.health_status)}
                      </div>
                    </div>
                    
                    {selectedNode?.name === node.name && (
                      <div className="mt-3 pt-3 border-t border-gray-600 text-xs space-y-2">
                        <div>
                          <span className="text-gray-400">Status:</span>
                          <span className="ml-2">{node.health_status}</span>
                        </div>
                        <div>
                          <span className="text-gray-400">Created:</span>
                          <span className="ml-2">
                            {new Date(node.creationTimestamp).toLocaleDateString()}
                          </span>
                        </div>
                        {node.ip_addresses.length > 0 && (
                          <div>
                            <span className="text-gray-400">IP Addresses:</span>
                            <div className="ml-2 space-y-1">
                              {node.ip_addresses.map((addr, idx) => (
                                <div key={idx} className="text-gray-300">
                                  {addr.type}: {addr.address}
                                </div>
                              ))}
                            </div>
                          </div>
                        )}
                        {node.podCIDR && (
                          <div>
                            <span className="text-gray-400">Pod CIDR:</span>
                            <span className="ml-2">{node.podCIDR}</span>
                          </div>
                        )}
                        {node.capacity.cpu && (
                          <div>
                            <span className="text-gray-400">CPU Capacity:</span>
                            <span className="ml-2">{node.capacity.cpu}</span>
                          </div>
                        )}
                        {node.capacity.memory && (
                          <div>
                            <span className="text-gray-400">Memory Capacity:</span>
                            <span className="ml-2">{node.capacity.memory}</span>
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default TopologyPage;